.month-selector {
  position: absolute;
  top: 80px;
  padding: 1rem 3.5rem;
  background: #999; }
  @media (max-width: 37.5em) {
    .month-selector {
      top: 45px; } }
  .month-selector .arrow {
    position: absolute;
    top: -8px;
    left: 65px;
    width: 20px;
    height: 20px;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    background: #999; }
  .month-selector > div {
    font-size: 2rem;
    color: #fff;
    cursor: pointer; }



